<script lang="ts">
  import { DATE_FORMAT } from '../../constants'
  import dayjs from 'dayjs'

  import { TimeType } from '../../types'
  import { getIntlContent } from '../../utility'
  import { since } from '../../ui-text'
  import { contentLangState } from '../../stores/ui'
  export let time: TimeType
  const { to, from } = time
</script>

<div
  class="absolute right-4 top-4 w-1/4 rotate-90 text-2xl font-bold opacity-60 print:static print:rotate-0 md:rotate-0 md:text-right"
>
  {#if dayjs(to).isSame(from)}
    <span>{from.format(DATE_FORMAT)}</span>
  {:else}
    {#if !to}<span
        >{getIntlContent(since, $contentLangState)}</span
      >{/if}
    <span>{from.format(DATE_FORMAT)}</span>
    {#if to}<span>{` - ${to.format(DATE_FORMAT)}`}</span
      >{/if}
  {/if}
</div>
